.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
}

.item{
    width: 100px;
    border-radius: 5px;
    margin: 10px ;
}

.img{
    width: 100%;
    border-radius: 5px;
    object-fit: cover;
    object-position: center center;
}
.name{
    color: wheat;
    font-size: 13px;
}